import {
  dayNouns,
  weekNouns,
  monthNouns,
  yearNouns,
  reviewNouns
} from '../../assets/js/variables.js';

import {
  getNoun
} from '../../assets/js/functions.js';

document.querySelectorAll('.review__amount').forEach((elem) => {
  const amountNumber = +(elem.querySelector('.review__amount-number').textContent);
  const amountMeasureUnitElem = elem.querySelector('.review__amount-measure-unit');

  amountMeasureUnitElem.textContent = getNoun(amountNumber, reviewNouns);
});

const getPassedTimeString = (reviewDate) => {

  let daysNumber = Math.floor((Date.now() - new Date(reviewDate)) / (1000 * 60 * 60 * 24));

  switch (true) {
    case (daysNumber < 0):
      return ' '
    case (daysNumber === 0):
      return '';
    case (daysNumber < 8):
      return (daysNumber + ' ' + getNoun(daysNumber, dayNouns) + ' ');
    case (daysNumber < 32):
      let weeksNumber = Math.floor(daysNumber / 7);
      return (weeksNumber + ' ' + getNoun(weeksNumber, weekNouns) + ' ');;
    case (daysNumber < 366):
      let monthsNumber = Math.floor(daysNumber / 31.4);
      return (monthsNumber + ' ' + getNoun(monthsNumber, monthNouns) + ' ');
    default:
      let yearsNumber = Math.floor(daysNumber / 365);
      return (yearsNumber + ' ' + getNoun(yearsNumber, yearNouns) + ' ');
  }

};



document.querySelectorAll('.review__time-passed').forEach((elem) => {
  elem.textContent = getPassedTimeString(elem.textContent);
});